@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,300;0,400;0,500;0,600;1,100;1,300&display=swap');

* {
    font-family: 'Poppins', sans-serif;
    margin: 0; padding: 0;
    box-sizing: border-box;
    outline: none;
    border: none;
    text-decoration: none;
    text-transform: capitalize;
    transition: .2 linear;
}

$green: #219150;
$black: #10221b;
$blue: #3b83bd;
$white: #fff;
$light-bg: #eee;
$border: .2rem solid $white;
$box-shadow: 0 .5rem 1rem rgba(0,0,0,.1);


@mixin grid($val) {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax($val, 1fr));
    gap: 1.5rem;
}


html{
    font-size: 62.5%;
    overflow-x: hidden;
    scroll-behavior: smooth;
    scroll-padding-top: 5rem ;

    &::-webkit-scrollbar{
        width:1rem;
    }

    &::-webkit-scrollbar-track{
        background: $white;
    }

    &::-webkit-scrollbar-thumb{
        background: $black;
    }

}

section{
    padding: 5rem 9%;
}


.btn {

    margin-top: 1rem;
    display: inline-block;
    border: $border;
    color: $black;
    cursor: pointer;
    background: none;
    font-size: 1.7rem;
    padding: 1rem 3rem;
    border-radius: 2rem;
    max-width: 20rem;
    transition-property: background-color  color font-size;
    transition-duration: 300ms;

    &__categoria {
        margin-top: 1rem;
        display: inline-block;
        border: .2rem solid $blue;
        color: $black;
        cursor: pointer;
        background: none;
        font-size: 1.7rem;
        padding: 1rem 3rem;
        border-radius: 2rem;
        max-width: 20rem;
        transition-property: background-color  color font-size;
        transition-duration: 300ms;
        
    }

    &__categoria:hover {
        background-color: $blue;
        color: $white;
        font-size: 2.2rem;
    }
    

    &:hover {
        background-color: $white;
        color: $blue;
        font-size: 2.2rem;
    }



}

.heading {
    text-align: center;
    margin-bottom: 2.5rem;
    font-size: 4rem;
    color: $black;
}



.header {
    position: fixed;
    top: 0; left: 0; right: 0;
    padding: 2rem 9%;
    z-index: 1000;
    display: flex;
    align-items: center;

    &.active {
        background: $white;
        box-shadow: $box-shadow;
    }

    .logo{
        margin-right: auto;
        font-size: 2.5rem;
        color: $black;
        font-weight: bolder;
 

    i{
        color: $blue;
    }
  }

  .navbar {

        a{
            margin-left: 2rem;
            font-size: 1.7rem;
            color: $black;

            &:hover {
                color: $blue;
            }
        }

        #nav-close {
            font-size: 5rem;
            cursor: pointer;
            color:  $blue;
            display: none;
        }
  }

  .icons a,
  .icons div {
      font-size: 2.5rem;
      margin-left: 2rem;
      cursor: pointer;
      color: $black;

      &:hover {
          color: $blue;
      }
  }

  #menu-btn {
    display: none;
  }

}


.search-form {
    position: fixed;
    top: 0; left: 0;
    height: 100%;
    width: 100%;
    background: rgba(0,0,0,.8);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 10000;
    transform: translateY(-110%);
    

    &.active {
        transform: translateY(0%);

    }

    #close-search {
        position: absolute;
        top: 1.5rem; right: 2.5rem;
        cursor: pointer;
        color: $white;
        font-size: 6rem;

        &:hover {
            color: $blue;
        }
    }

    form {
        width: 70rem;
        margin: 0 2rem;
        border-bottom: .2rem solid white;
        display: flex;
        align-items: center;
    
        input {
            width: 100%;
            font-size: 2rem;
            color: $white;
            text-transform: none;
            background: none;
            padding: 2rem;

            &::placeholder {
                color: #aaa;
            }
        }

        label {
            font-size: 3rem;
            cursor: pointer;
            color: $white;

            &:hover {
                color: $blue;
            }
        }
    }
}

.home {

    padding: 0;

    .box {
        min-height: 100vh;
        display: flex;
        align-items: center;
        background-size: cover !important;
        background-position: center !important;
        padding: 2rem 9%;

       

        .second {
            justify-content: flex-start;
        }

    }

}

.content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
   
  
    span, h3,p,a {
        grid-column: 2;
    }

    span {
        font-size: 4rem;
        color: $black;
    }

    h3 {
        font-size: 6rem;
        color: $blue;
        padding-top: .5rem;
        text-transform: uppercase;
    }

    p {
        line-height: 2;
        color: $black;
        font-size: 1.5rem;
        padding: 1rem 0;
    }


}

.content-2 {
    display: grid;
    grid-template-columns: repeat(2, 1fr);

    span, h3,p,a {
        grid-column: 1;
    }

    span {
        font-size: 4rem;
        color: $black;
    }

    h3 {

        font-size: 6rem;
        color: $blue;
        padding-top: .5rem;
        text-transform: uppercase;
    }

    
    p {
        line-height: 2;
        color: $black;
        font-size: 1.5rem;
        padding: 1rem 0;
    }
}

.swiper-button-next::after,
.swiper-button-prev::after {
    font-size: 2rem;
    color: $black;
}

// Contentenido 


.category {

    .box-container {
        @include grid(29rem);

        .box {
            text-align: center;
            padding: 2rem;
        }

        img {
            height: 20rem;
            width: 20rem;
            border-radius: 50%;
            margin-bottom: 1rem;
        }

        h3 {
            font-size: 2rem;
            color: $blue;
        }

        p {
            font-size: 1.5rem;
            color: $black;
            padding: 1rem 0;
            line-height: 2;
        }


    }
}

// SECCION 

.about {
    background: $blue;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 2rem;

    .image {
        flex: 1 1 42rem;
        padding-right: 5rem;
        padding-bottom: 5rem;

        img {
            width: 100%;
            box-shadow:  4rem 4rem 0 rgba(255,255,255,);
        }
    }

    .contenido {
        flex: 1 1 42rem;
       

        h3 {
            font-size: 5rem;
            color: #fff;
        }

        p {
            font-size: 1.7rem;
            color: $black;
            padding: 1rem 0;
            line-height: 2;
            font-weight: 600;
            justify-content: flex-end;
        }       
    }
}

.btn__contenido {
    margin-top: 1rem;
    display: inline-block;
    border: $border;
    color: $blue;
    cursor: pointer;
    background-color: $white;
    font-size: 1.7rem;
    padding: 1rem 3rem;
    border-radius: 2rem;
    max-width: 20rem;
    transition-property: font-size;
    transition-duration: 300ms;
}

.btn__contenido:hover {
    font-size: 2rem;
}

.shop {

    .slide {
       
        border: .2rem solid $black;
        border-radius: 2rem;

        &:hover .image .icons {
            transform: translateY(0);
        }

        .image,
        .icons {
            transition-property: all;
            transition-duration: 300ms;
        }

        .image{
            position: relative;
            overflow: hidden;
            height: 30rem;
            width: 100%;

            img {
                height: 100%;
                width: 100%;
                object-fit: cover;
                border-radius: 2rem;
            }

            .icons {
                width: 100%;
                position: absolute;
                bottom: 2rem; left:0;
                text-align: center;
                z-index: 10;
                transform: translateY(7rem);

                a {
                    height: 4.5rem;
                    width: 4.5rem;
                    line-height: 4.5rem;
                    font-size: 1.7rem;
                    background: $black;
                    color: $white;
                    margin: 0 .2rem;

                    &:hover {
                        background-color: $blue;
                    }
                }
            }
        }

        .compras {
            padding: 1rem 0;
            text-align: center;

            h3 {
                font-size: 2rem;
                color: $black;
            }

            .price {
                padding: 1rem 0;
                padding: .5rem;
                font-size: 2.5rem;
                color: $green;
            }

            .stars i {
                font-size: 1.7rem;
                color: $blue;
            }
        }
    }
}

.packages {
    background: $blue;

    .heading {
        color: #fff;
    }

    .btn__packages {
        margin-top: 1rem;
        display: inline-block;
        border: $border;
        color: $white;
        cursor: pointer;
        background-color: $blue;
        font-size: 1.7rem;
        padding: 1rem 3rem;
        border-radius: 2rem;
        max-width: 20rem;
        transition-property: font-size;
        transition-duration: 300ms;
    }

    .btn__packages:hover {
        font-size: 2.2rem
    }

    .box-container {
        @include grid(32rem);
        .box {
            text-align: center;
            background: $white;
            border: $border;
            box-shadow: $box-shadow;
            border-radius: 2rem;

            .image {
                height: 25rem;
                overflow: hidden;
                padding: 2rem;
                padding-bottom: 0;
                border-radius: .5rem;

                img {
                    height: 100%;
                    width: 100%;
                    object-fit: cover;
                    border-radius: 2rem;
                }
            }

            .contento {
                padding: 2rem;

                h3{
                    font-size: 2rem;
                    color: $black;
                }

                p {
                    font-size: 1.5rem;
                    color: $black;
                    padding: 1rem 0;
                    line-height: 2;
                }

                .price {
                    font-size: 2rem;
                    color: #219150;
                }
            }
        }
    }
    
}

.services {
    background: $light-bg;

    .box-container {
        @include grid(31rem);

        .box {
            text-align: center;
            padding: 5rem;
            border: $box-shadow;
            border-radius: 1rem;
            background: $white;
            transition-property: padding;
            transition-duration: 300ms;

            &:hover {
                padding: 3rem;
                background: $blue;

                img{
                    filter: invert(1);
                }

                h3 {
                    color: $white;
                }

                p{
                    color: $white;
                }

                .btn__blue {
                    background-color: $white;
                    color: $blue;
                }
            }

            img{ 
                height: 10rem;
                margin-bottom: 1rem;
            }

            h3 {
                font-size: 2rem;
                color:  $blue;
            }

            p {
                font-size: 1.5rem;
                color: $black;
                padding: 1rem 1;
                line-height: 1.5;
            }

            .btn__blue {
                margin-top: 1rem;
                display: inline-block;
                border: $border;
                color: $white;
                cursor: pointer;
                background-color: $blue;
                font-size: 1.7rem;
                padding: 1rem 3rem;
                border-radius: 2rem;
                max-width: 20rem;
                transition-property: background-color  color font-size;
                transition-duration: 300ms;
                
            }
        }
    }


}


.review {
    
    .slide{

        .text {
            padding: 2rem;
            font-size: 1.5rem;
            font-style: italic;
            background: $light-bg;
            border-radius: .5rem;
            color: $black;
            line-height: 2;
            position: relative;
            z-index: 0;
            margin-bottom: 3rem;

            &::before {
                content: '';
                position: absolute;
                bottom: -1.5rem; left: .7rem;
                height: 3rem;
                width: 3rem;
                background: $light-bg;
                transform: rotate(45deg);
            }
        }

        .user {
            display: flex;
            align-items: center;
            gap: 1rem;

            img {
                height: 7rem;
                width: 7rem;
                border-radius: 50%;
            }

            h3{
                font-size: 2rem;
                color: $black;
            }

            span {
                color: $blue;
                font-size: 1.6rem;
            }
        }
    }
}























//Media Queries 

@media (max-width:1200px) {

    .header {
        padding: 2rem;
    }

    section{
        padding: 3rem 2rem;
    }
}


@media (max-width:991px) {

    html {
        font-size: 55%;
        scroll-padding-top: 7rem;
    }

    .content {
        grid-template-columns: repeat(2,1fr);
       
        span,h3,p,a {
            grid-column: 1;
        }

    }


    .home .box {
        justify-content: center;
        background-position: right !important;

        &.second {
            justify-content: center;
            background-position: left !important;
        }
    }

  
}

@media (max-width:768px) {

    .header {

        #menu-btn {
            display: inline-block;
        }

        .navbar {
            position: fixed;
            top: 0; left: -110%;
            background: $white;
            z-index: 10000;
            width: 35rem;
            height: 100%;
            display: flex;
            flex-flow: column;
            align-items: center;
            justify-content: center;

            &.active {
                left: 0;
                box-shadow: 0 0 0 100vw rgba(0,0,0,.8);
            }

            a {
                margin: 1rem 0;
                font-size: 3rem;
            }

            #nav-close {
                display: block;
                position: absolute;
                top: 1rem; right: 2rem;
            }
        }

    }
}

@media (max-width:450px) {

    html {
        font-size: 50%;
    }
}


