@import url("https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,300;0,400;0,500;0,600;1,100;1,300&display=swap");
* {
  font-family: "Poppins", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
  border: none;
  text-decoration: none;
  text-transform: capitalize;
  transition: 0.2 linear;
}

html {
  font-size: 62.5%;
  overflow-x: hidden;
  scroll-behavior: smooth;
  scroll-padding-top: 5rem;
}
html::-webkit-scrollbar {
  width: 1rem;
}
html::-webkit-scrollbar-track {
  background: #fff;
}
html::-webkit-scrollbar-thumb {
  background: #10221b;
}

section {
  padding: 5rem 9%;
}

.btn {
  margin-top: 1rem;
  display: inline-block;
  border: 0.2rem solid #fff;
  color: #10221b;
  cursor: pointer;
  background: none;
  font-size: 1.7rem;
  padding: 1rem 3rem;
  border-radius: 2rem;
  max-width: 20rem;
  transition-property: background-color color font-size;
  transition-duration: 300ms;
}
.btn__categoria {
  margin-top: 1rem;
  display: inline-block;
  border: 0.2rem solid #3b83bd;
  color: #10221b;
  cursor: pointer;
  background: none;
  font-size: 1.7rem;
  padding: 1rem 3rem;
  border-radius: 2rem;
  max-width: 20rem;
  transition-property: background-color color font-size;
  transition-duration: 300ms;
}
.btn__categoria:hover {
  background-color: #3b83bd;
  color: #fff;
  font-size: 2.2rem;
}
.btn:hover {
  background-color: #fff;
  color: #3b83bd;
  font-size: 2.2rem;
}

.heading {
  text-align: center;
  margin-bottom: 2.5rem;
  font-size: 4rem;
  color: #10221b;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: 2rem 9%;
  z-index: 1000;
  display: flex;
  align-items: center;
}
.header.active {
  background: #fff;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
}
.header .logo {
  margin-right: auto;
  font-size: 2.5rem;
  color: #10221b;
  font-weight: bolder;
}
.header .logo i {
  color: #3b83bd;
}
.header .navbar a {
  margin-left: 2rem;
  font-size: 1.7rem;
  color: #10221b;
}
.header .navbar a:hover {
  color: #3b83bd;
}
.header .navbar #nav-close {
  font-size: 5rem;
  cursor: pointer;
  color: #3b83bd;
  display: none;
}
.header .icons a,
.header .icons div {
  font-size: 2.5rem;
  margin-left: 2rem;
  cursor: pointer;
  color: #10221b;
}
.header .icons a:hover,
.header .icons div:hover {
  color: #3b83bd;
}
.header #menu-btn {
  display: none;
}

.search-form {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
  transform: translateY(-110%);
}
.search-form.active {
  transform: translateY(0%);
}
.search-form #close-search {
  position: absolute;
  top: 1.5rem;
  right: 2.5rem;
  cursor: pointer;
  color: #fff;
  font-size: 6rem;
}
.search-form #close-search:hover {
  color: #3b83bd;
}
.search-form form {
  width: 70rem;
  margin: 0 2rem;
  border-bottom: 0.2rem solid white;
  display: flex;
  align-items: center;
}
.search-form form input {
  width: 100%;
  font-size: 2rem;
  color: #fff;
  text-transform: none;
  background: none;
  padding: 2rem;
}
.search-form form input::placeholder {
  color: #aaa;
}
.search-form form label {
  font-size: 3rem;
  cursor: pointer;
  color: #fff;
}
.search-form form label:hover {
  color: #3b83bd;
}

.home {
  padding: 0;
}
.home .box {
  min-height: 100vh;
  display: flex;
  align-items: center;
  background-size: cover !important;
  background-position: center !important;
  padding: 2rem 9%;
}
.home .box .second {
  justify-content: flex-start;
}

.content {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.content span, .content h3, .content p, .content a {
  grid-column: 2;
}
.content span {
  font-size: 4rem;
  color: #10221b;
}
.content h3 {
  font-size: 6rem;
  color: #3b83bd;
  padding-top: 0.5rem;
  text-transform: uppercase;
}
.content p {
  line-height: 2;
  color: #10221b;
  font-size: 1.5rem;
  padding: 1rem 0;
}

.content-2 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.content-2 span, .content-2 h3, .content-2 p, .content-2 a {
  grid-column: 1;
}
.content-2 span {
  font-size: 4rem;
  color: #10221b;
}
.content-2 h3 {
  font-size: 6rem;
  color: #3b83bd;
  padding-top: 0.5rem;
  text-transform: uppercase;
}
.content-2 p {
  line-height: 2;
  color: #10221b;
  font-size: 1.5rem;
  padding: 1rem 0;
}

.swiper-button-next::after,
.swiper-button-prev::after {
  font-size: 2rem;
  color: #10221b;
}

.category .box-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(29rem, 1fr));
  gap: 1.5rem;
}
.category .box-container .box {
  text-align: center;
  padding: 2rem;
}
.category .box-container img {
  height: 20rem;
  width: 20rem;
  border-radius: 50%;
  margin-bottom: 1rem;
}
.category .box-container h3 {
  font-size: 2rem;
  color: #3b83bd;
}
.category .box-container p {
  font-size: 1.5rem;
  color: #10221b;
  padding: 1rem 0;
  line-height: 2;
}

.about {
  background: #3b83bd;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 2rem;
}
.about .image {
  flex: 1 1 42rem;
  padding-right: 5rem;
  padding-bottom: 5rem;
}
.about .image img {
  width: 100%;
  box-shadow: 4rem 4rem 0 white;
}
.about .contenido {
  flex: 1 1 42rem;
}
.about .contenido h3 {
  font-size: 5rem;
  color: #fff;
}
.about .contenido p {
  font-size: 1.7rem;
  color: #10221b;
  padding: 1rem 0;
  line-height: 2;
  font-weight: 600;
  justify-content: flex-end;
}

.btn__contenido {
  margin-top: 1rem;
  display: inline-block;
  border: 0.2rem solid #fff;
  color: #3b83bd;
  cursor: pointer;
  background-color: #fff;
  font-size: 1.7rem;
  padding: 1rem 3rem;
  border-radius: 2rem;
  max-width: 20rem;
  transition-property: font-size;
  transition-duration: 300ms;
}

.btn__contenido:hover {
  font-size: 2rem;
}

.shop .slide {
  border: 0.2rem solid #10221b;
  border-radius: 2rem;
}
.shop .slide:hover .image .icons {
  transform: translateY(0);
}
.shop .slide .image,
.shop .slide .icons {
  transition-property: all;
  transition-duration: 300ms;
}
.shop .slide .image {
  position: relative;
  overflow: hidden;
  height: 30rem;
  width: 100%;
}
.shop .slide .image img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  border-radius: 2rem;
}
.shop .slide .image .icons {
  width: 100%;
  position: absolute;
  bottom: 2rem;
  left: 0;
  text-align: center;
  z-index: 10;
  transform: translateY(7rem);
}
.shop .slide .image .icons a {
  height: 4.5rem;
  width: 4.5rem;
  line-height: 4.5rem;
  font-size: 1.7rem;
  background: #10221b;
  color: #fff;
  margin: 0 0.2rem;
}
.shop .slide .image .icons a:hover {
  background-color: #3b83bd;
}
.shop .slide .compras {
  padding: 1rem 0;
  text-align: center;
}
.shop .slide .compras h3 {
  font-size: 2rem;
  color: #10221b;
}
.shop .slide .compras .price {
  padding: 1rem 0;
  padding: 0.5rem;
  font-size: 2.5rem;
  color: #219150;
}
.shop .slide .compras .stars i {
  font-size: 1.7rem;
  color: #3b83bd;
}

.packages {
  background: #3b83bd;
}
.packages .heading {
  color: #fff;
}
.packages .btn__packages {
  margin-top: 1rem;
  display: inline-block;
  border: 0.2rem solid #fff;
  color: #fff;
  cursor: pointer;
  background-color: #3b83bd;
  font-size: 1.7rem;
  padding: 1rem 3rem;
  border-radius: 2rem;
  max-width: 20rem;
  transition-property: font-size;
  transition-duration: 300ms;
}
.packages .btn__packages:hover {
  font-size: 2.2rem;
}
.packages .box-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(32rem, 1fr));
  gap: 1.5rem;
}
.packages .box-container .box {
  text-align: center;
  background: #fff;
  border: 0.2rem solid #fff;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
  border-radius: 2rem;
}
.packages .box-container .box .image {
  height: 25rem;
  overflow: hidden;
  padding: 2rem;
  padding-bottom: 0;
  border-radius: 0.5rem;
}
.packages .box-container .box .image img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  border-radius: 2rem;
}
.packages .box-container .box .contento {
  padding: 2rem;
}
.packages .box-container .box .contento h3 {
  font-size: 2rem;
  color: #10221b;
}
.packages .box-container .box .contento p {
  font-size: 1.5rem;
  color: #10221b;
  padding: 1rem 0;
  line-height: 2;
}
.packages .box-container .box .contento .price {
  font-size: 2rem;
  color: #219150;
}

.services {
  background: #eee;
}
.services .box-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(31rem, 1fr));
  gap: 1.5rem;
}
.services .box-container .box {
  text-align: center;
  padding: 5rem;
  border: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
  border-radius: 1rem;
  background: #fff;
  transition-property: padding;
  transition-duration: 300ms;
}
.services .box-container .box:hover {
  padding: 3rem;
  background: #3b83bd;
}
.services .box-container .box:hover img {
  filter: invert(1);
}
.services .box-container .box:hover h3 {
  color: #fff;
}
.services .box-container .box:hover p {
  color: #fff;
}
.services .box-container .box:hover .btn__blue {
  background-color: #fff;
  color: #3b83bd;
}
.services .box-container .box img {
  height: 10rem;
  margin-bottom: 1rem;
}
.services .box-container .box h3 {
  font-size: 2rem;
  color: #3b83bd;
}
.services .box-container .box p {
  font-size: 1.5rem;
  color: #10221b;
  padding: 1rem 1;
  line-height: 1.5;
}
.services .box-container .box .btn__blue {
  margin-top: 1rem;
  display: inline-block;
  border: 0.2rem solid #fff;
  color: #fff;
  cursor: pointer;
  background-color: #3b83bd;
  font-size: 1.7rem;
  padding: 1rem 3rem;
  border-radius: 2rem;
  max-width: 20rem;
  transition-property: background-color color font-size;
  transition-duration: 300ms;
}

.review .slide .text {
  padding: 2rem;
  font-size: 1.5rem;
  font-style: italic;
  background: #eee;
  border-radius: 0.5rem;
  color: #10221b;
  line-height: 2;
  position: relative;
  z-index: 0;
  margin-bottom: 3rem;
}
.review .slide .text::before {
  content: "";
  position: absolute;
  bottom: -1.5rem;
  left: 0.7rem;
  height: 3rem;
  width: 3rem;
  background: #eee;
  transform: rotate(45deg);
}
.review .slide .user {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.review .slide .user img {
  height: 7rem;
  width: 7rem;
  border-radius: 50%;
}
.review .slide .user h3 {
  font-size: 2rem;
  color: #10221b;
}
.review .slide .user span {
  color: #3b83bd;
  font-size: 1.6rem;
}

@media (max-width: 1200px) {
  .header {
    padding: 2rem;
  }

  section {
    padding: 3rem 2rem;
  }
}
@media (max-width: 991px) {
  html {
    font-size: 55%;
    scroll-padding-top: 7rem;
  }

  .content {
    grid-template-columns: repeat(2, 1fr);
  }
  .content span, .content h3, .content p, .content a {
    grid-column: 1;
  }

  .home .box {
    justify-content: center;
    background-position: right !important;
  }
  .home .box.second {
    justify-content: center;
    background-position: left !important;
  }
}
@media (max-width: 768px) {
  .header #menu-btn {
    display: inline-block;
  }
  .header .navbar {
    position: fixed;
    top: 0;
    left: -110%;
    background: #fff;
    z-index: 10000;
    width: 35rem;
    height: 100%;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: center;
  }
  .header .navbar.active {
    left: 0;
    box-shadow: 0 0 0 100vw rgba(0, 0, 0, 0.8);
  }
  .header .navbar a {
    margin: 1rem 0;
    font-size: 3rem;
  }
  .header .navbar #nav-close {
    display: block;
    position: absolute;
    top: 1rem;
    right: 2rem;
  }
}
@media (max-width: 450px) {
  html {
    font-size: 50%;
  }
}